@import "./Vars.scss";
@import "./Fonts.scss";

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  position: relative;
  background: $white;
  overflow-x: hidden;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a{
  text-decoration: none;
  &:hover{
  }
  &:visited{
  }
}

.container{
  width: calc(100% - 40px);
  max-width: 1080px;
  margin: auto;
}

.masonry{
  padding-bottom: 20px;

  &__item{
    padding: 0 20px;
    margin: 0 0 50px;
    width: 100%;
    height: 100%;
    display: block;
    cursor: pointer;
  }
}

.edit-button{
  cursor: pointer;
  position: fixed;
  bottom: 40px;
  right: 40px;

  width: 80px;
  height: 80px;

  font-size: 50px;

  display: flex;
  justify-content: center;
  align-items: center;

  border: 5px solid darkred;
  color: darkred;
  border-radius: 10px;

  &[data-editing="true"]{
    border-color: darkgreen;
    color: darkgreen;
  }
}

.post-preview{
  position: relative;
  display: block;

  &:hover{
    .post-preview__name,
    .art__title{
      transform: translateY(0) skewY(0);
      transition: transform 0.4s, opacity 0.4s;
      
      opacity: 1;
    }
  }
  
  &__image{
    width: 100%;
    height: 100%;
    display: block;
  }
  &__name,
  .art__title{
    justify-content: start;
    position: relative;
    margin: 22px 0 12px;
    font-family: "Oswald";
    text-transform: uppercase;
    font-size: 15px;

    color: $black;

    transform: translateY(30px) skewY(-8deg);
    opacity: 0;
  }
  &__hover{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: none;
  }
  &__remove-button{
    cursor: pointer;
    width: 40px;
    height: 40px;
    position: absolute;
    right: 0;
    top: 0;
    background: transparent;
    border: 2px solid transparent; 
    border-radius: 4px;
    font-size: 30px;
    color: red;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover{
      border-color: $lightbrown;
      background: rgba($lightbrown, 0.4);
    }
  }
  &__text{
    display: inline-block;
    font-size: 20px;
    font-family: "Lato";
    color: $white;
    background: rgba($black, 0.3);
    padding: 20px;
  }
  &__order-button{
    padding: 10px;
    font-size: 30px;
    width: 50px;
    height: 50px;

    display: inline-flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 50%;
    
    &--left{
      
      left: 0;
    }
    &--right{
      right: 0;
    }
  }
}

.post {
  &__container{
    margin: 0 80px;
    max-width: 100%;
    display: flex;
    @media (max-width: 1024px) {
      flex-direction: column;
    }
    @media (max-width: 500px) {
      margin: 0 40px;
    }
  }
  &__image-wrapper{
    max-width: 1100px;
    flex: 0 0 66.67%;
  }
  &__main-image-block {

  }
  &__main-image {
    width: 100%;
    height: auto;
    margin-bottom: 30px;
  }
  
  &__image-block {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
  }
  &__image {
    width: 100%;
    height: auto;
  }
  &__text-block {
    flex: 0 0 33.33%;
    padding: 0 30px 0 15px;
    @media (max-width: 1024px) {
      padding-top: 30px;
    }
  }
  &__name {
    font-family: "Oswald";
    text-transform: uppercase;
    font-size: 17px;
    margin-bottom: 8px;
  }
  &__description {
    font-family: "Open sans";
    font-size: 14px;
  }
}