@import "./Vars.scss";
@import "./Fonts.scss";

.about{

  &__container{
    display: flex;
    flex-direction: row;
    gap: 40px;
    align-items: flex-start;
    padding: 50px;
    @media (max-width: 1024px) {
      flex-direction: column;
      padding: 20px;
      align-items: center;
    }
  }
  &__image{
    max-width: 500px;
    display: block;
    object-fit: cover;
    width: 100%;
    height: auto;
    flex: 0 1 auto;
  }
  &__text-block{
    display: flex;
    flex-direction: column;
  }
  &__title{
    font-family: "Oswald";
    text-transform: uppercase;
    font-size: 85px;
    margin-bottom: 50px;
  }
  &__text{
    font-family: "Open sans";
    color: $grey;
    font-size: 18px;
    max-width: 500px;
  }
}